.navbar {
    display: block;
    background-color: white;
    height: 100%;
    width: 300px;
    margin-right: 10px;
    transition: all 300ms 0s ease-in-out;
    padding: 5px 13px;
    position: fixed;
    top: 70px;
    left: 0;
    z-index: 1;

    ul {
        padding: 0;
    }
}